<template>
    <nav :class="[{'-ontop': isOnTop }]">
        <div class="logo">
            <AppLogo />
        </div>

        <div :class="['hamburger-menu', { '-is-opened': menuIsOpened }]" @click="menuIsOpened = !menuIsOpened">
            <div class="bar-top"></div>
            <div class="bar-middle"></div>
            <div class="bar-bottom"></div>
        </div>
        
        <div v-if="menuIsOpened" class="mobile-menu">
            <div class="links">
                <a href="#about">Who I am</a>
                <a href="#testemonials">Testemonials</a>
                <button>Contact me</button>
            </div>
        </div>
    </nav>
</template>

<script>
import AppLogo from '../assets/images/logo.svg?inline'
export default {
    components: {
        AppLogo
    },

    data() {
        return {
            menuIsOpened: false,
            isOnTop: true
        }
    },

    created () {
        window.addEventListener('scroll', this.handleScroll);
    },
    mounted () {
        this.handleScroll()
    },
    unmounted () {
        window.removeEventListener('scroll', this.handleScroll);
    },
    methods: {
        handleScroll () {
            if (window.pageYOffset === 0) {
                this.isOnTop = true
            } else {
                this.isOnTop = false
            }
        }
    }

}
</script>

